import React from "react";
import axios from "axios";
import Moives from "../Components/Movies";
import "./Home.css"

class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      isLoading: true,
      movies: []
    }
    
  }
  
  getMovies = async () => {
    const {
      data: {
        data: {
          movies
        }
      }
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort=rating")
    this.setState({ movies, isLoading: false})
  }

  componentDidMount(){
    this.getMovies()
   }

  render() {
    const { isLoading, movies } = this.state 
    return (
      <section className="container">
        { 
          isLoading ? 
          (<div className="loader"> <h2 className="loader__text"> Now loading... </h2> </div>)
            : <div className="movies">
                <ul className="movie__list">
                { movies.map((movie) => (<Moives key={movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image} genres={movie.genres}/>))}
                </ul>
              </div>
        }  
      </section>
    )
  }
}

export default App